import {simpleDateTime} from '../../filter/time'
import {MessageBox, Notification} from 'element-ui'
import UserInputSelection from '../../../backyard/user/widget/UserInputSelection'
import BaseEntity from '../base/BaseEntity'
import Filter from '../base/Filter'
import Tank from '../tank/Tank'
import User from '../user/User'
import Tag from '../tag/Tag'

export default class Article extends BaseEntity {

  constructor(args) {
    super(args)

    //
    this.userUuid = null

    //
    this.title = null

    //
    this.tags = null

    //
    this.posterTankUuid = null

    //Url
    this.posterUrl = null

    //
    this.digest = null

    //markdown
    this.isMarkdown = true

    //markdown
    this.markdown = null

    //html
    this.html = null

    //
    this.privacy = false

    //
    this.top = false

    //
    this.agree = 0

    //
    this.words = 0

    //
    this.hit = 1

    //
    this.commentNum = 0

    //
    this.needNotify = true

    //
    this.posterTank = new Tank('image', false, 1024 * 1024, '1M')

    //
    this.user = new User()

    //
    this.agreed = false

    //
    this.tagArray = []


  }


  static URL_API_ARTICLE_TOP = '/article/top'
  static URL_API_ARTICLE_CANCEL_TOP = '/article/cancel/top'

  getFilters() {
    return [
      new Filter(Filter.prototype.Type.SORT, '', 'orderSort'),
      new Filter(Filter.prototype.Type.SORT, '', 'orderTop'),
      new Filter(Filter.prototype.Type.SORT, '', 'orderHit'),
      new Filter(Filter.prototype.Type.SORT, '', 'orderPrivacy'),
      new Filter(Filter.prototype.Type.SORT, '', 'orderCreateTime'),
      new Filter(Filter.prototype.Type.HTTP_INPUT_SELECTION, '', 'userUuid', null, User, false, UserInputSelection),
      new Filter(Filter.prototype.Type.CHECK, '', 'privacy'),
      new Filter(Filter.prototype.Type.INPUT, '', 'title'),
      new Filter(Filter.prototype.Type.INPUT, '', 'keyword')
    ]
  };

  render(obj) {
    super.render(obj)
    this.renderEntity('posterTank', Tank)
    this.renderEntity('user', User)
    this.renderList('tagArray', Tag)
  }

  getForm() {
    return {
      title: this.title,
      tags: this.tags,
      posterTankUuid: this.posterTankUuid,
      posterUrl: this.posterUrl,
      digest: this.digest,
      isMarkdown: this.isMarkdown,
      markdown: this.markdown,
      html: this.html,
      words: this.words,
      privacy: this.privacy,
      top: this.top,
      needNotify: this.needNotify,
      uuid: this.uuid ? this.uuid : null
    }
  }

  validate() {
    if (this.posterTank) {
      this.posterTankUuid = this.posterTank.uuid
      this.posterUrl = this.posterTank.url
    }

    if (!this.title) {
      this.errorMessage = "";
      return false
    }

    if (!this.markdown || this.markdown.length > 2147483647) {
      this.errorMessage = "2147483647";
      return false
    }

    if (!this.html || this.html.length > 2147483647) {

      this.errorMessage = "2147483647";
      return false
    }



    this.errorMessage = null;
    return true
  }


  httpChangeTop(successCallback, errorCallback) {
    let that = this
    let confirmText = ''
    if (this.top) {
      confirmText = ''
    }
    MessageBox.confirm(confirmText, '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    }).then(function () {
        that.httpPost(that.top ? Article.URL_API_ARTICLE_CANCEL_TOP : Article.URL_API_ARTICLE_TOP, {articleUuid: that.uuid}, function (response) {
          typeof successCallback === 'function' && successCallback(response)
        })

      },
      function () {
      }
    )
  }

}



